import { useContext, useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import ChangeLanguage from "../../components/change-language/change-language";
import { GameBanner } from "../../components/game-banner/game-banner";
import GameContext from "../../contexts/game";
import { games, IGame } from "../../models/games";
import mainBackground from "./../../assets/images/main_background.jpg";
import Styles from "./games.module.css";

export const Games = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [backgroundUrl, setBackgroundUrl] = useState(mainBackground);
  const { setGame } = useContext(GameContext);

  const styles = {
    mainBackground: {
      background: `url(${backgroundUrl}) no-repeat 50% fixed`,
      display: "flex",
      justifyContent: "center",
    },
  };

  const goToGame = (game: IGame) => {
    setGame(game);
    navigate('/game');
  }

  return (
    <div style={styles.mainBackground} className="main-background">
      <div className={Styles.container}>
        <label className={Styles.title}>{t("choose_a_game")}</label>
        <div className={Styles.gamesContainer}>
          {games.map((game) => (
            <div
              key={`games.${game.key}`}
              onMouseEnter={() => setBackgroundUrl(game.background)}
              onMouseLeave={() => setBackgroundUrl(mainBackground)}
              onClick={() => goToGame(game)}
            >
              <GameBanner
                background={game.background}
                key={`games.${game.key}`}
                name={`${t(`games.${game.key}`)} ${game.emoji}`}
              ></GameBanner>
            </div>
          ))}
        </div>
        <ChangeLanguage></ChangeLanguage>
      </div>
    </div>
  );
};

export default Games;
