import { useTranslation } from 'react-i18next';
import enFlag from './../../assets/images/flags/en.svg';
import esFlag from './../../assets/images/flags/es.svg';
import ptFlag from './../../assets/images/flags/pt.svg';
import Styles from './change-language.module.css';

const ChangeLanguage = () => {
  const { i18n } = useTranslation();
  return (
    <div className={Styles.flagContainer}>
      <div>
        <img onClick={() => i18n.changeLanguage("en")} alt="en_flag" src={enFlag}></img>
        <img onClick={() => i18n.changeLanguage("es")} alt="es_flag" src={esFlag}></img>
        <img onClick={() => i18n.changeLanguage("pt")} alt="pt_flag" src={ptFlag}></img>
      </div>
    </div>
  )
}

export default ChangeLanguage;
