/* eslint-disable react-hooks/exhaustive-deps */
import { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import FloatButton from "../../components/float-button/float-button";
import InGame from "../../components/in-game/in-game";
import Instructions from "../../components/instructions/instructions";
import GameContext from "../../contexts/game";
import { GameState } from "../../models/game-state";
import BackIcon from "./../../assets/icons/back.svg";
import GamesIcon from "./../../assets/icons/games.svg";
import Styles from "./game.module.css";

export const Game = () => {
  const { game } = useContext(GameContext);
  const [gameState, setGameState] = useState(GameState.Instructions);

  const { t } = useTranslation();

  const navigate = useNavigate();

  useEffect(() => {
    if (!game) {
      navigate("/games");
    }
  }, []);

  if (!game) {
    return <label>No game</label>;
  }

  let state;
  let backIcon;
  switch (gameState) {
    case GameState.Instructions:
      state = <Instructions setGameState={setGameState}></Instructions>;
      backIcon = GamesIcon;
      break;
    case GameState.In_Game:
      state = <InGame></InGame>;
      backIcon = BackIcon;
      break;
  }

  const styles = {
    mainBackground: {
      background: `url(${game?.background}) no-repeat 50% fixed`,
      display: "flex",
      justifyContent: "center",
      alignItems: "normal"
    },
  };

  const back = () => {
    switch (gameState) {
      case GameState.Instructions:
        navigate("/games");
        break;
      case GameState.In_Game:
        setGameState(GameState.Instructions);
        break;
    }
  };

  return (
    <div style={styles.mainBackground} className="main-background">
      <div className={Styles.backButton} onClick={back}>
        <FloatButton icon={backIcon}></FloatButton>
      </div>
      <div>
        <div className={Styles.gameTitleContainer}>
          <label className={Styles.gameTitle}>
            {t(`games.${game.key}`)} {game.emoji}
          </label>
        </div>
        {state}
      </div>
    </div>
  );
};
