import { times } from "lodash";
import { useContext } from "react";
import { useTranslation } from "react-i18next";
import GameContext from "../../contexts/game";
import { GameState } from "../../models/game-state";
import BoxContainer from "../box-container/box-container";
import GameButton from "../game-button/game-button";
import Styles from "./instructions.module.css";

interface Props {
  setGameState: any;
}

const Instructions = (props: Props) => {
  const { game, setGame } = useContext(GameContext);
  const { t } = useTranslation();

  const generateParagraph = (_item: unknown, index: number) => (
    <p className={Styles.paragraph} key={`game_instructions_${index}`}>
      {t(`gamesInstructions.${game.key}.paragraph${++index}`)}
    </p>
  );

  return (
    <div>
      <BoxContainer>
        <h2 className={Styles.title}>{t("instructions")}</h2>
        {times(game.instructions.paragraphs).map(generateParagraph)}
      </BoxContainer>
      <div className={Styles.buttonContainer}>
        {game.categories.map((category, index) => (
    
            <GameButton
              backgroundImage={category.background}
              label={`${t(category.label)} ${category.emoji}`}
              onClick={() => {
                props.setGameState(GameState.In_Game)
                setGame({ ...game, categoryKey: category.categoryKey });
              }}
              key={index}
            ></GameButton>
        ))}
      </div>
    </div>
  );
};

export default Instructions;
