
import Styles from './home.module.css';
import mainBackground from './../../assets/images/main_background.jpg';
import logo from './../../assets/images/logo.svg';
import cups from './../../assets/images/cups.png';
import { NeonButton } from '../../components/neon-button/neon-button';
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import ChangeLanguage from '../../components/change-language/change-language';

const styles = {
  mainBackground: {
    background: `url(${mainBackground}) no-repeat 50% fixed`,
    display: "flex",
    justifyContent: "center",
  },
  button: {
    width: '400px',
    margin: 'auto',
  }
}

export const Home = () => {
  const { t } = useTranslation();

  const navigate = useNavigate();

  const joinGluglup = () => {
    navigate("/games");
  }

  return (
    <div style={styles.mainBackground} className="main-background">
      <div className={Styles.container}>
        <img className={Styles.logo} alt="logo" src={logo} />
        <img className={Styles.cups} alt="cups" src={cups} />
        <NeonButton onClick={joinGluglup} style={styles.button} label={t('play')}></NeonButton>
        <ChangeLanguage></ChangeLanguage>
      </div>

    </div>
  );
}

export default Home;