/* eslint-disable react-hooks/exhaustive-deps */
import { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import GameContext from "../../contexts/game";
import { blueBackground, disableBackground } from "../../models/backgrounds";
import BoxContainer from "../box-container/box-container";
import GameButton from "../game-button/game-button";
import Styles from "./in-game.module.css";
import { questions as Questions } from "./../../models/questions";
import { cloneDeep, get, sample, size } from "lodash";

const InGame = () => {
  const { game } = useContext(GameContext);
  const { t, i18n } = useTranslation();

  const filterByCategory = (question: any) => {
    const categoryKey = game.categoryKey as string;
    if (categoryKey) {
      return get(question, categoryKey);
    }
    return true;
  };

  const defaultQuestions = cloneDeep(get(Questions, game.key));

  const [question, setQuestion] = useState<any>(null);

  const [showNext, setShowNext] = useState(true);

  const [questions, setQuestions] = useState(defaultQuestions);

  const [counter, setCounter] = useState(5);

  const changeQuestion = () => {
    let questionsFiltered = questions.filter(filterByCategory);

    if (!size(questionsFiltered)) {
      questionsFiltered = get(Questions, game.key).filter(filterByCategory);
    }

    const newQuestion: any = sample(questionsFiltered);

    questionsFiltered = questionsFiltered.filter(
      (q: any) => q.en !== newQuestion.en
    );
    setQuestion(newQuestion);
    setQuestions(questionsFiltered);
  };

  useEffect(() => {
    changeQuestion();
  }, []);

  const nextQuestion = () => {
    setShowNext(false);
    changeQuestion();
  };

  let nextButton;

  if (showNext) {
    nextButton = (
      <GameButton
        onClick={nextQuestion}
        backgroundImage={blueBackground}
        label={t(`nextLabels.${game.nextLabel}`)}
      ></GameButton>
    );
  } else {
    if (counter === 5) {
      setTimeout(() => {
        setCounter(4);
        setTimeout(() => {
          setCounter(3);
          setTimeout(() => {
            setCounter(2);
            setTimeout(() => {
              setCounter(1);
              setTimeout(() => {
                setCounter(5);
                setShowNext(true);
              }, 1000);
            }, 1000);
          }, 1000);
        }, 1000);
      }, 1000);
    }

    nextButton = (
      <GameButton
        backgroundImage={disableBackground}
        label={`${counter}`}
      ></GameButton>
  
    );
  }

  return (
    <div>
      <BoxContainer>
        <div className={Styles.questionContainer}>
          <p>{get(question, i18n.language)}</p>
        </div>
      </BoxContainer>
      <div className="textCenter">{nextButton}</div>
    </div>
  );
};

export default InGame;
